import React from 'react'
import { IUser } from '../../types/types'
import { Formik, Form, useField } from 'formik'
import { Button, TextField, Select, MenuItem } from '@material-ui/core'
import s from './AddTaskForm.module.css'
import * as Yup from 'yup'
import { addTodo } from '../../redux/tasks-reducer'
import { useDispatch } from 'react-redux'

const MyTextInput = ({ label, ...props }: any) => {
  const [field, meta] = useField(props)

  return (
    <div className={s.wrapper}>
      <TextField fullWidth label="–ù–∞–∑–≤–∞–Ω–∏–µ –∑–∞–¥–∞—á–∏" {...field} {...props}/>
      {meta.touched && meta.error && <div className={s.error}>{meta.error}</div>}
    </div>
  )
}

const MySelect = ({ label, ...props }: any) => {
  const [field, meta] = useField(props)

  return (
    <div className={s.wrapper}>
      <Select fullWidth {...field} {...props}>
        {props.users?.map((user: any) => (
          <MenuItem key={user.id} value={user.username}>{user.username}</MenuItem>
        ))}
      </Select>
      {meta.touched && meta.error && <div className={s.error}>{meta.error}</div>}
    </div>
  )
}

interface IProps {
  users: Array<IUser> | undefined
}

const AddTaskForm: React.FC<IProps> = ({ users }) => {
  const dispatch = useDispatch()

  return (
    <Formik
      initialValues={{
        title: '',
        user: '',
      }}
      validationSchema={Yup.object({
        title: Yup.string()
          .max(15, '–ú–∞–∫—Å–∏–º—É–º 15 —Å–∏–º–≤–æ–ª–æ–≤')
          .required('–ù–∞–∑–≤–∞–Ω–∏–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        user: Yup.string().required('–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),
      })}
      onSubmit={(values) => {
        dispatch(addTodo(
          values.title,
          users?.find(user => user.username === values.user)?.id
        ))
      }}
    >
      <Form>
        <div className={s.form}>
          <MyTextInput
            name="title"
          />
          <MySelect
            name="user"
            users={users}
          />
          <Button variant="contained" color="primary" type="submit">–°–æ–∑–¥–∞—Ç—å –∑–∞–¥–∞—á—É</Button>
        </div>
      </Form>
    </Formik>
  )
}

export default React.memo(AddTaskForm)
