import React from 'react'
import { IUser } from '../../types/types'
import { Formik, Form, useField } from 'formik'
import {
  Button,
  TextField,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio
} from '@material-ui/core'
import s from './SearchForm.module.css'
import * as Yup from 'yup'

const MyTextInput = ({ label, ...props }: any) => {
  const [field, meta] = useField(props)

  return (
    <div className={s.wrapper}>
      <TextField fullWidth label="–ù–∞–∑–≤–∞–Ω–∏–µ –∑–∞–¥–∞—á–∏" {...field} {...props}/>
      {meta.touched && meta.error && <div className={s.error}>{meta.error}</div>}
    </div>
  )
}

const MyRadioButton = ({ label, ...props }: any) => {
  const [field, meta] = useField(props)

  return (
    <div className={s.wrapper}>
      <FormControl component="fieldset">
        <FormLabel component="legend">–ü—Ä–æ–≥—Ä–µ—Å—Å –∑–∞–¥–∞—á–∏</FormLabel>
        <RadioGroup aria-label="gender" name="gender1" onChange={() => {}}>
          <FormControlLabel {...field} {...props} value="–í—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è" control={<Radio/>} label="–í—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è"/>
          <FormControlLabel {...field} {...props} value="–ù–µ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è" control={<Radio/>} label="–ù–µ –≤—ã–ø–ª–Ω–µ–Ω–Ω–∞—è"/>
          <FormControlLabel {...field} {...props} value="–õ—é–±–∞—è" control={<Radio/>} label="–õ—é–±–∞—è"/>
        </RadioGroup>
      </FormControl>
      {meta.touched && meta.error && <div className={s.error}>{meta.error}</div>}
    </div>
  )
}

interface IProps {
  users: Array<IUser> | undefined
  setSearchString: (string: string | null) => void
  setCompletedMode: (string: '–í—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è' | '–ù–µ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è' | '–õ—é–±–∞—è') => void
}

const SearchForm: React.FC<IProps> = ({ users, setSearchString, setCompletedMode }) => {
  return (
    <Formik
      initialValues={{
        searchString: '',
        completedFind: '',
      }}
      validationSchema={Yup.object({
        searchString: Yup.string()
          .max(15, '–ú–∞–∫—Å–∏–º—É–º 15 —Å–∏–º–≤–æ–ª–æ–≤')
          .required('–ù–∞–∑–≤–∞–Ω–∏–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'),
        completedFind: Yup.string().required('–í—ã–±–æ—Ä –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω'),
      })}
      onSubmit={(values) => {
        setSearchString(values.searchString)
        // @ts-ignore
        setCompletedMode(values.completedFind)
      }}
    >
      <Form>
        <div className={s.form}>
          <MyRadioButton
            name="completedFind"
            users={users}
          />
          <div className={s.search}>
            <MyTextInput
              name="searchString"
            />
            <Button variant="contained" color="primary" type="submit">–ù–∞–π—Ç–∏</Button>
            <Button style={{marginLeft: '20px'}} onClick={() => {
              setSearchString(null)
              setCompletedMode('–õ—é–±–∞—è')
            }} variant="contained" color="primary" type="button">–°–±—Ä–æ—Å</Button>
          </div>
        </div>
      </Form>
    </Formik>
  )
}

export default SearchForm
